/* TODO general header styles (fixed to the top of the screen) */

.app-header {
    position: sticky;
    top: 0;
    background: white;
    border-bottom: solid 1px #ccc;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    min-height: 50px;
    flex-shrink: 1;
    z-index: 10;
}

.app-header .header-search {
    position: absolute;
    right: 10px;
    width: 300px
}

.app-header .expand-button {
    position: absolute;
    left: 10px;
}