.app-feed {
    margin: 10px;
    padding: 10px;
    flex-grow: 1;
    overflow: scroll;
    position: relative;
}

.app-feed-background-overlay {
    background: rgba(255, 255, 255, 0.98);
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    height: 100%;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}

.app-feed-background-image {
    background-image: url("https://1000logos.net/wp-content/uploads/2020/09/Game-of-Thrones-logo.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    position: absolute;
    z-index: 1;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
}

.app-feed-prompt {
    display: flex;
    justify-content: center;
    height: 100%;
    align-items: center;
    position: relative;
    z-index: 3;
}
.app-feed-grid {
    position: relative;
    z-index: 3;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    grid-template-rows: 1fr;
}